export default {
	data() {
		return {
			idx:"-1",
			list:[],
			designArea:'',
			listItem:''
		};
	},
	props:{
		backImg:{
			type:String
		}
	},
	methods:{
		// 选择类别
		select(index,item){
			this.idx = index;
			this.listItem = item;
			this.$emit('designArea',this.idx,this.designArea,item);
		},
		// 输入框失去焦点
		onBlur(){
			this.$emit('designArea',this.idx,this.designArea,this.listItem);
			if(this.designArea !== ''){
				this.designArea = this.designArea + 'm²';
			}
		},
		// 输入框获得焦点
		onFocus(){
			if(this.designArea !== ''){
				this.designArea = this.designArea.slice(0,this.designArea.indexOf('m²'));
			}	
		}
	},
	mounted(){
		this.$emit('designArea',this.idx,this.designArea);
		let categorys;
		if (uni.getStorageSync("catalogues")) {
			categorys = uni.getStorageSync("catalogues").categorys;
		} else if (this.$store.state.catalogues) {
			categorys = this.$store.state.catalogues.categorys;
		}
		if (categorys && categorys.length > 0) {
			let list= categorys.filter((item)=>{
				return item.type == 1;
			})
			this.list = list;
			// this.list.pop();
		}	
	}
}
